import classNames from "classnames";
import { string, arrayOf } from "prop-types";

import styles from "./Card.scss";

const Profile = props => (
  <div className={styles.card}>
    <img src={props.image} />
    <h3>{props.name}</h3>
    <ul className={styles.social}>
      {props.linkedin && (
        <li>
          <a target="_blank" href={props.linkedin} className={classNames(styles.icon, styles.linkedin)} />
        </li>
      )}
    </ul>
  </div>
);

Card.propTypes = {
  name: string.isRequired,
  role: string.isRequired,
  image: string.isRequired,
  experience: arrayOf(string).isRequired,
  linkedin: string,
  twitter: string,
  github: string,
  dribbble: string
};

Card.defaultProps = {
  linkedin: null,
  twitter: null,
  github: null,
  dribbble: null
};

export default Profile;
